<template>
  <div>
    <div class="page-article d-flex jc-center" v-if="model">
      <div class="d-none left"></div>
      <div class="page">
        <div class="text-green fs-xxxxl mt-11">{{ model.title }}</div>
        <div class="text-grey-2 fs-sm mt-6">
          <p>{{ model.createdAt | date("YYYY-MM-DD HH:mm") }}</p>
        </div>
        <div>
          <div class="p-2 bdr post-tags text-border text-center bg-blue fs-sm mt-6 hand">
            <span class>
              <i class="iconfont icon-tag"></i>
            </span>
            &nbsp;
            <span class>{{ model.categories[0].name }}</span>
          </div>
        </div>
        <div class="text-grey-2 fs-md mb-9 container">
          <div class="markdown-body">
            <div id="content" v-html="model.body"></div>
          </div>
        </div>
      </div>
      <div class="d-none left">
        <div class="blogs-menu pl-4 toc-sticky text-grey-1 pl-9">
          <div>
            <h2></h2>
            <div
              class="menu-title hand text-ellipsis"
              :title="item.text"
              v-for="item in articleToc"
              :key="item.id"
              :style="{ paddingLeft: `${item.indent}em` }"
              @click="scrollTo(item.id)"
            >{{ item.text }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="art-comment">
      <h3 class="fs-xxxxl"></h3>
      <comment-textarea
        class="textarea-box bg-white bdr"
        model="comments"
        @toResponse="getBlogsComments"
        type="parent"
        :blogsId="id"
        placeholder=""
      ></comment-textarea>
      <comment-list
        class="mt-7 mb-10"
        model="comments"
        @getCommentList="getBlogsComments"
        :commentsList="parentComments"
        v-if="parentComments"
        :blogsId="id"
      ></comment-list>
    </div>
    <div class="w-100 h-100">
      <el-backtop :bottom="50"></el-backtop>
    </div>
  </div>
</template>

<script>
import marked from "marked";
import hljs from "highlight.js";
import "highlight.js/styles/tomorrow-night-eighties.css";
import { addLineAndCopy } from "../common/lineAndCopy";
import Toc from "../common/Toc.js";
import commentTextarea from "../components/commentTextarea";
import commentList from "../components/commentList";

const renderer = new marked.Renderer();

marked.setOptions({
  renderer: renderer,
  gfm: true,
  tables: true,
  breaks: true,
  pedantic: false,
  sanitize: false,
  smartLists: true,
  smartypants: false,
  highlight: function(code, lang) {
    if (lang && hljs.getLanguage(lang)) {
      return hljs.highlight(lang, code, true).value;
    } else {
      return hljs.highlightAuto(code).value;
    }
  }
});

export default {
  components: {
    commentTextarea,
    commentList
  },
  props: {
    id: { required: true }
  },
  data() {
    return {
      model: null,
      articleToc: [],
      parentComments: []
    };
  },
  watch: {
    id: "fetch"
    // id(){
    //   this.fetch()
    // }
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`articles/list/${this.id}`);
      this.model = res.data;
      let tocData = Toc(marked(res.data.body));
      this.model.body = tocData.article;
      this.articleToc = tocData.toc;
      this.$nextTick(() => {
        addLineAndCopy();
        // this.addCodeSupport()
      });
    },
    scrollTo(id) {
      //  toc 
      let node = document.querySelector('[data-id="' + id + '"]');
      if (!node) {
        return;
      }
      node.scrollIntoView({
        behavior: "smooth",
        block: "center",
        inline: "nearest"
      });
    },
    async getBlogsComments() {
      let res = await this.$http.get(`/comments/${this.id}`);
      let blogsComments = res.data;
      this.parentComments = blogsComments.filter(
        v => v.parent == "5e90abb3a6522a44580faa1c"
      );
      this.parentComments.forEach(c => {
        return (c.children = blogsComments.filter(v => v.parent == c._id));
      });
    }
  },
  created() {
    this.fetch();
    this.getBlogsComments();
  }
};
</script>
